.login__wrapper {
    display: grid;
    grid-template-rows: auto 1fr auto;
    background: linear-gradient(180deg, rgba(217, 217, 217, 0) 0%, rgba(58, 125, 232, 0.1) 100%);

    .forgot-password {
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

.password-wrapper {
    position: relative;
    .toggle-password {
        position: absolute;
        
        font-weight: 700;
        background: none;
        border: none;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        display: flex;    
        span {
            color: #7EA1F3;
            font-size: 30px;
            font-weight: bold;
        }    
    }
}

.login__header {
    p {
        font-weight: 400;
        font-size: 13px;
        color: #77787B;
    }

    .login__cta {
        display: flex;
        align-items: center;
        gap: 20px;
    }

   
}


.login__form {
    border: none;    

    .card-body {
        background: #FFFFFF;
        box-shadow: 0 35px 100px rgba(58, 125, 232, 0.2);
        border-radius: 5px;
        border: none;
    }
    
    .form-control {
        font-size: 14px;

    }

    h2 {
        font-family: 'Work Sans';
        font-style: normal;
        font-weight: 600;
        font-size: 30px;
        line-height: 35px;
        text-align: center;
        color: #222326;
    }
    label {
        color: #838383;
    }

    .form-select {
        color: #838383;
        font-size: 14px;
    }

    .btn-submit {
        align-items: center;
        display: flex;
        justify-content: center;
    }    
}

.login__footer {
    text-align: center;
    padding-block: 30px;
    padding-inline: 15px;
    p {
        font-weight: 400;
        font-size: 13px;
        color: #77787B;
        margin-bottom: 0;
        ~ p {
            margin-top: 5px;
        }
    }

    .login__cta {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    .btn-help {

        display: flex;
        margin-inline: auto;
        margin-block-start: 15px;        


        width: 25px;
        height: 25px;
        padding: 0;
        border-radius: 50%;
        border: solid 2px;
        align-items: center;
        justify-content: center;        
        .icon {
            font-size: 18px;
        }
        @media screen and (min-width: 1024px) {
            position: fixed;
            right: 30px;
            bottom: 30px;
        }
    }
}